@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Roboto:wght@300;400;900&display=swap');

.links-wrapper{
  display: flex;
  width: 500px;
  font-weight: 300;
  justify-content: space-between;
  font-size: 15px;
  margin-top: -20px;
  color: lightgrey;
}

.links-button{
  color: lightgrey;
  transition: .5s;
  text-decoration: none;

}

.links-button:hover{
  color: PaleVioletRed;
  transition: .5s ease-out;
  cursor: pointer;
}
