}

@-webkit-keyframes slideUp{
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  100% {
    -webkit-transform: translate(0px, -45px);
            transform: translate(0px, -45px);
  }
}

@keyframes slideUp{
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
  100% {
    -webkit-transform: translate(0px, -45px);
            transform: translate(0px, -45px);
  }
}

@-webkit-keyframes slideDown{
  0% {
    -webkit-transform: translate(0px, -45px);
            transform: translate(0px, -45px);
  }
  100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

@keyframes slideDown{
  0% {
    -webkit-transform: translate(0px, -45px);
            transform: translate(0px, -45px);
  }
  100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }
}

@-webkit-keyframes inAnimation {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes inAnimation {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@-webkit-keyframes outAnimation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes outAnimation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@-webkit-keyframes portfolio-transition{
  0%{
    opacity:0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes portfolio-transition{
